.anim-down {
  position: relative;
  -webkit-animation: animdown .5s ease-in-out .0s 1 normal forwards;
          animation: animdown .5s ease-in-out .0s 1 normal forwards;
}

@-webkit-keyframes animdown {
  0% {
    opacity: 0;
    top: -10px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

@keyframes animdown {
  0% {
    opacity: 0;
    top: -10px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

.anim-trans {
  -webkit-transition: height .2s ease-in-out;
  transition: height .2s ease-in-out;
}

