  transition: .4s background ease-in-out;
}

.form-inp,
.option-cust {
  position: relative;
  background-color: #fff;
  padding: 12px 16px;
  border: none;
  border-radius: 5px;
  -webkit-transition: .4s background ease-in-out;
  transition: .4s background ease-in-out;
}

input.form-inp[type="submit"] {
  cursor: pointer;
  background-color: #224774;
  color: #fff;
  -webkit-transition: .2s ease-in-out background;
  transition: .2s ease-in-out background;
}

