  padding: 8px 16px;
  border-radius: 0;
  border-bottom: 1px solid #1d1d1d15;
  color: #1d1d1db9;
  list-style: none;
}

.option-cust:hover {
  background-color: #1d4f8b;
  color: #fff;
}

input.form-inp[type="submit"]:hover {
  background-color: #133764;
  -webkit-transition: .2s ease-in-out background;
  transition: .2s ease-in-out background;
}


