#auth {
  background-color: #dddddd;
  overflow-y: auto;
}

#auth article {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: linear-gradient(#22477449, #22477449), url(./auth.jpg) center center/cover no-repeat;
}

#auth article h2 {
  font-size: 42px;
  color: #fff;
}

#auth article>p {
  color: #fff;
  font-size: 20px;
}

#auth .side-note {
  width: 100%;
}

#auth-register,
#auth-login {
  height: 100vh;
  overflow-y: auto;
  z-index: 2;
  background-color: #e9e9e9;
}